.button {
    border-radius: 7px;
    border-color: transparent;
    color: #ffffff;
    font-size: 15px;
    font-weight: 700;
    line-height: 15px;
    padding: 7px 7px;
    width: max-content;
    min-width: 100px;
    cursor: pointer;

    &.cursorDefault {
        cursor: default;
    }
}
